.locations {
  padding-top: 32px;
  padding-bottom: 32px;
  padding-left: 60px;
  padding-right: 60px;
}

.modal-loc__title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  margin-top: 0;

  font-family: 'Seymour One', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #131313;

  @media screen and (min-width: 768px) {
    margin-bottom: 32px;
    font-size: 20px;
    line-height: 1.4;
  }
}

.modal-loc__map {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  min-width: 224px;
  min-height: 199px;
  margin-bottom: 24px;
  border-radius: 10px;

  @media screen and (min-width: 768px) {
    min-width: 448px;
    min-height: 260px;
    margin-bottom: 32px;
  }
}

.modal-loc__map-open {
  display: flex;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.25;
  color: #131313;
  background-color: #f8d24e;
  border-radius: 10px;
  width: 134px;
  height: 42px;

  transition-property: opacity;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.21;
    width: 145px;
  }
  &:hover {
    background-color: #0a8817;
  }
  &:focus {
    background-color: #0a8817;
    color: #fcfcfc;
  }
}

